<script lang="ts">
    import DevOutTable from "./DevOutTable.svelte";
    import { amZug, state, stellung, togreRunning } from "../stores";
    import T from "../togre";
    import H from "../core";
    import { download } from "../tools";

    const db = T.stdDB;

    function start () {
        $togreRunning = true;
        window.setTimeout(() => {
            const result = db.calc(H.convert.c($stellung), $amZug, optMeth, prefdb);
            const output = [
                result.s + "<br>Player." + result.p,
                (result.optionMethod) + "<br>" + (result.prefdb ? "Ja" : "Nein"),
                `<h3 style="font-size: 200%; color: red; display: inline; margin: 20px">${result.t}</h3>`,
                result.time.toLocaleString() + "ms",
                (result.db.xnew + result.db.onew).toLocaleString()
            ];
            out.log(output);
            $togreRunning = false;
        }, 1000);
    }

    function dbclear () {
        const len = db.len();
        db.clear();
        if (len) out.log("Datenbank wurde geleert: " + len.toLocaleString() + " Eintrge gelscht.");
    }

    function save () {
        download("Download.togreDB.json", db.stringify(prompt("Bitte gib einen Kommentar fr diesen Download an. Er wird zusammen mit den Datenbankinhalten in der heruntergeladenen Datei gesichert, damit du spter nachvollziehen kannst, woher dieser Download stammt.")));
    }

    let out: DevOutTable;
    // let logfin: boolean = true;
    let prefdb: boolean = true;
    let showOpts = false;
    let optMeth: "pure" | "logfin" | "preffin" = "pure";
</script>

<h1 style="display:inline;">TOGRE-Rechner:</h1>

<button id="start" on:click={start} disabled={$state === "aktiv"}>Start</button>
<button on:click={() => {out.clear(); if (db.len()) out.log("Ausgabe gelscht. " + db.len().toLocaleString() + " Eintrge in der DB.");}} disabled={$state === "aktiv"}>Ausgabe lschen</button>
<button on:click={dbclear} disabled={$state === "aktiv"}>Datenbank leeren</button>

<button style="font-size: 8pt;"><label>
    <input type="checkbox" style="margin-bottom: 0;" bind:checked={showOpts}> Optimierungsdetails anzeigen</label>
</button>
<table style="display:{showOpts ? '' : 'none'};"><tr><td>
    <label style="display: inline;">Optionsmethode: <select bind:value={optMeth}>
        <option value="pure">Syntaktisch ("pure")</option>
        <option value="logfin">LogFin-Eigenprfung ("logfin")</option>
        <option value="preffin">LogFin-Optionsprfung ("preffin")</option>
    </select></label><br>
    <small>Die Methode "logfin" fhrt meist zu einer Verschlechterung der Performance, reduziert aber die DB-Belastung. Bei "preffin" ist dieser Effekt noch strker.</small>
<!--     
    <label style="display: inline;"><input type="checkbox" bind:checked={logfin}> LogFin-Prfung</label><br>
    <small>Die logische Finalittsprfung (LogFin) kann die Belastung der Datenbank verringern und zur Beschleunigung des Rechenprozesses fhren. Je nach Konstellation ist aber auch eine Verlangsamung mglich. Das Ergebnis wird jedoch nicht beeinflusst.</small> -->
</td><td>
    <label style="display: inline;"><input type="checkbox" bind:checked={prefdb}> prefDB-Variante</label><br>
    <small>Hier wird unter den Optionen zunchst nach vorhandenen Eintrgen in der Datenbank gesucht, bevor tatschlich rekursiv gerechnet wird. Dies kann die Datenbank entlasten und die Performance verbessern.</small>
</td></tr></table>
    
<DevOutTable bind:this={out} keys={["Konstellation", "OptMeth<br>prefDB", "Ergebnis", "Zeit", "Neue<br>DB-Eintrge"]}/>
<br>
<button on:click={save} title="Die aktuellen Inhalte der Datenbank werden komprimiert kodiert und in deinem Downloads-Ordner als 'Download.togreDB.json' gesichert.">Datenbankinhalte als Datei sichern</button>


<style>
    #start {
        font-size: 20pt;
    }
</style>